var store = require('../store')

module.exports = {
  props: ['tab'],
  data: function() {
    return {
      topics: []
    }
  },
  compiled: function() {
    this.update()
  },
  methods: {
    update: function() {
      store.fetchTopics()
        .then(function(result) {
          // console.log(result)
          this.topics = result.data
        }.bind(this))
    }
  }
}


